@import '~normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400&display=swap');
$orange: #db5940;
$light-grey: #e6e6e6;
$grey: #9a98a2;
$dark-grey: #605f64;
$dark-background: #38373b;
$small-icon: 70px;
%flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

%horizontal-centering {
  display: flex;
  flex-direction: column;
  align-items: center;
}

%vertical-centering {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

%simple-text {
  font-size: 1.6rem;
  letter-spacing: 1px;
  line-height: 2.6rem;
  color: $grey;
  font-weight: 400;
  font-family: "Source Sans Pro";
}

// disable margins
h1,
h2,
p {
  margin: 0;
}

// disable decoration
a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style-type: none;
}

// default size to 1rem
html {
  font-size: 10px;
}

.header {
  background-color: $orange;
  color: white;
  &__container {
    height: 80px;
    @extend %flex-between;
    .header__site-name {
      font-size: 2.4rem;
      letter-spacing: 1px;
      font-weight: 300;
      font-family: 'Source Sans Pro', sans-serif;
    }
  }
}

.navigator {
  width: 410px;
  @extend %flex-between;
  &__item {
    font-size: 1.6rem;
    letter-spacing: 1px;
    font-weight: 400;
    font-family: 'Source Sans Pro', sans-serif;
  }
}

.container {
  max-width: 1170px;
  margin: 0px auto;
}

.wrapper_dark {
  background-color: $dark-background;
}

.welcome {
  letter-spacing: 5px;
  font-weight: 300;
  color: $light-grey;
  font-family: 'Source Sans Pro', sans-serif;
  text-align: center;
  height: 140px;
  @extend %vertical-centering;
  &__title {
    text-transform: uppercase;
    font-size: inherit;
    font-weight: 300;
    font-size: 3rem;
  }
  &__text {
    font-size: 2rem;
  }
}

.image {
  &__container {
    max-width: 1920px;
    height: 400px;
    background: url('../assets/img/welcome.webp');
    background-size: cover;
    background-position: center;
    margin: 0 auto;
  }
}

.quote {
  @extend %horizontal-centering;
  &__icon {
    margin-bottom: 34px;
  }
  &__title {
    margin-bottom: 29px;
    font-size: 28px;
    letter-spacing: 1px;
    color: $dark-grey;
    font-weight: 300;
    font-family: "Source Sans Pro";
    text-align: center;
  }
  &__text {
    margin-bottom: 28px;
    width: 318px;
    text-align: center;
    @extend %simple-text
  }
}

.icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  border: 3px solid #a6a4ad;
}

.icon_small {
  width: $small-icon;
  min-width: $small-icon;
  height: $small-icon;
  border-radius: 35px;
}

#plane {
  margin-left: -5px;
}

#heart-icon {
  // correct not symmetric image
  padding-left: 5px;
}

.row {
  padding: 80px 0;
  @extend %flex-between
}

.article {
  display: grid;
  grid-column-gap: 30px;
  grid-template-areas: 'content icon';
  &__content {
    grid-area: content;
    text-align: right;
  }
  &__icon {
    width: 201px;
    height: 201px;
    border-radius: 101px;
    background-position: center;
    grid-area: icon;
  }
  &__icon_child {
    background-image: url('../assets/img/child.webp');
  }
  &__icon_birds {
    background-image: url('../assets/img/birds.webp');
  }
  &__icon_jesus {
    background-image: url('../assets/img/jesus.webp');
  }
  &__icon_tree {
    background-image: url('../assets/img/tree.webp');
  }
}

.article_reverse {
  grid-template-areas: 'icon content';
}

.content {
  width: 318px;
  @extend %vertical-centering;
  &__title {
    font-size: 2.8rem;
    letter-spacing: 1px;
    color: $dark-grey;
    font-weight: 300;
    font-family: "Source Sans Pro";
    margin-bottom: 31px;
  }
  &__text {
    font-size: 1.6rem;
    letter-spacing: 1px;
    line-height: 2.6rem;
    color: $grey;
    font-weight: 400;
    font-family: "Source Sans Pro";
  }
}

.content_left {
  text-align: left;
  width: 302px;
  &__title {
    margin-bottom: 25px;
  }
}

.articles {
  display: grid;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
  grid-template-columns: 1fr 1fr;
  padding: 80px 0px 110px 0px;
}

.more-link {
  color: $orange;
}

.contacts {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 80px 0px;
}

.contact {
  display: flex;
  align-items: center;
  flex-basis: $small-icon;
  &__text {
    max-width: 250px;
    @extend %simple-text;
  }
  &__icon {
    margin-right: 30px;
  }
}